<template>
	<view class="activity-page">
		<view class="page-header">
			<text class="page-title">Ê¥ªÂä®</text>
			<view class="search-btn" @tap="showSearch">üîç</view>
		</view>

		<view class="filter-tabs">
			<scroll-view scroll-x class="filter-scroll" :show-scrollbar="false">
				<view class="filter-item"
					v-for="tab in filterTabs"
					:key="tab.value"
					:class="{ active: currentFilter === tab.value }"
					@tap="changeFilter(tab.value)">
					<text class="filter-text">{{ tab.label }}</text>
				</view>
			</scroll-view>
		</view>

		<scroll-view class="activity-list" scroll-y
			:refresher-enabled="true"
			:refresher-triggered="isRefreshing"
			@refresherrefresh="onRefresh"
			@scrolltolower="onLoadMore">

			<view v-if="loading && page === 1" class="loading-state">Âä†ËΩΩ‰∏≠...</view>

			<template v-else-if="activities.length > 0">
				<view class="activity-card"
					v-for="activity in activities"
					:key="activity.id"
					@tap="viewActivity(activity)">

					<view class="activity-header">
						<view class="activity-sequence">#{{ activity.sequence_number }}</view>
						<view class="activity-type">{{ getTypeInfo(activity.activity_type).icon }}</view>
						<view class="activity-status" :style="{ color: getStatusInfo(activity).color }">
							{{ getStatusInfo(activity).text }}
						</view>
					</view>

					<view class="activity-content">
						<text class="activity-title">{{ activity.title }}</text>
						<text class="activity-desc" v-if="activity.description">{{ activity.description }}</text>

						<view class="activity-details">
							<view class="detail-item">
								<text class="detail-icon">üìÖ</text>
								<text class="detail-text">{{ formatDate(activity.start_time) }}</text>
							</view>
							<view class="detail-item" v-if="activity.location">
								<text class="detail-icon">üìç</text>
								<text class="detail-text">{{ activity.location }}</text>
							</view>
							<view class="detail-item">
								<text class="detail-icon">üë•</text>
								<text class="detail-text">
									{{ activity.registration_count || 0 }}{{ activity.max_participants ? `/${activity.max_participants}` : '' }} ‰∫∫ÂèÇ‰∏é
								</text>
							</view>
						</view>

						<view class="activity-footer">
							<text class="creator">Áî± {{ activity.creator_name }} ÂàõÂª∫</text>
						</view>
					</view>
				</view>

				<view v-if="loadingMore" class="load-more">Âä†ËΩΩÊõ¥Â§ö...</view>
				<view v-if="!hasMore && activities.length > 0" class="no-more">Ê≤°ÊúâÊõ¥Â§öÊ¥ªÂä®‰∫Ü</view>
			</template>

			<view v-else-if="!loading" class="empty-state">
				<text class="empty-icon">üìÖ</text>
				<text class="empty-title">ÊöÇÊó†Ê¥ªÂä®</text>
				<text class="empty-subtitle">Âø´Êù•ÂàõÂª∫Á¨¨‰∏Ä‰∏™Ê¥ªÂä®ÂêßÔºÅ</text>
			</view>
		</scroll-view>

		<view class="create-btn" @tap="createActivity">+</view>
	</view>
</template>

<script>
	import { activityApi } from '../../api/index.js'
	import { showSuccess, showError, formatDate } from '../../utils/index.js'

	export default {
		data() {
			return {
				activities: [],
				loading: false,
				loadingMore: false,
				isRefreshing: false,
				page: 1,
				pageSize: 10,
				hasMore: true,
				searchVisible: false,
				searchKeyword: '',
				currentFilter: 'all',
				filterTabs: [
					{ label: 'ÂÖ®ÈÉ®', value: 'all' },
					{ label: 'Âç≥Â∞ÜÂºÄÂßã', value: 'upcoming' },
					{ label: 'ËøõË°å‰∏≠', value: 'ongoing' },
					{ label: 'Â∑≤ÁªìÊùü', value: 'completed' }
				]
			}
		},

		onLoad() {
			this.resetAndLoad()
		},

		onShow() {
			// Â¶ÇÊûúÊòØÁ¨¨‰∏ÄÊ¨°Âä†ËΩΩÊàñ‰ªéÂÖ∂‰ªñÈ°µÈù¢ËøîÂõûÔºåÂà∑Êñ∞Êï∞ÊçÆ
			if (!this.loadedOnce) {
				this.resetAndLoad()
				this.loadedOnce = true
			}
		},

		methods: {
			// ÈáçÁΩÆÂπ∂Âä†ËΩΩ
			resetAndLoad() {
				this.page = 1
				this.activities = []
				this.hasMore = true
				this.loadActivities()
			},

			// ÊòæÁ§∫ÊêúÁ¥¢
			showSearch() {
				// TODO: ÂÆûÁé∞ÊêúÁ¥¢ÂäüËÉΩ
				console.log('ÊòæÁ§∫ÊêúÁ¥¢')
			},

			// ÂàáÊç¢Á≠õÈÄâ
			changeFilter(filter) {
				if (this.currentFilter === filter) return
				this.currentFilter = filter
				this.resetAndLoad()
			},

			// Ëé∑ÂèñÁ≠õÈÄâÂèÇÊï∞
			getFilterParams() {
				const params = {
					page: this.page,
					limit: this.pageSize
				}

				// Ê∑ªÂä†Áä∂ÊÄÅÁ≠õÈÄâ
				if (this.currentFilter === 'upcoming') {
					params.status = 'registration'
				} else if (this.currentFilter === 'ongoing') {
					params.status = 'ongoing'
				} else if (this.currentFilter === 'completed') {
					params.status = 'completed'
				}

				// Ê∑ªÂä†ÊêúÁ¥¢ÂÖ≥ÈîÆËØç
				if (this.searchKeyword) {
					params.search = this.searchKeyword
				}

				return params
			},

			// Âä†ËΩΩÊ¥ªÂä®ÂàóË°®
			async loadActivities(isLoadMore = false) {
				if (isLoadMore) {
					if (!this.hasMore || this.loadingMore) return
					this.loadingMore = true
				} else {
					this.loading = true
				}

				try {
					const params = this.getFilterParams()
					const response = await activityApi.getList(params)

					if (response.success) {
						const { activities, pagination } = response.data
						const newActivities = Array.isArray(activities) ? activities : []

						if (isLoadMore) {
							// Âä†ËΩΩÊõ¥Â§öÔºåËøΩÂä†Êï∞ÊçÆ
							this.activities = [...this.activities, ...newActivities]
							// Â¢ûÂä†È°µÁ†ÅÔºåÂáÜÂ§á‰∏ãÊ¨°Âä†ËΩΩ
							this.page++
						} else {
							// Âà∑Êñ∞ÊàñÈ¶ñÊ¨°Âä†ËΩΩÔºåÊõøÊç¢Êï∞ÊçÆ
							this.activities = newActivities
							// ÈáçÁΩÆÈ°µÁ†Å‰∏∫ÂΩìÂâçÈ°µ
							this.page = pagination?.page || 1
						}

						// Êõ¥Êñ∞ÂàÜÈ°µÁä∂ÊÄÅ
						if (pagination) {
							this.hasMore = pagination.page < pagination.pages
						}
					}
				} catch (error) {
					console.error('Âä†ËΩΩÊ¥ªÂä®Â§±Ë¥•:', error)
					showError('Âä†ËΩΩÊ¥ªÂä®Â§±Ë¥•')
				} finally {
					if (isLoadMore) {
						this.loadingMore = false
					} else {
						this.loading = false
					}
				}
			},

			// ‰∏ãÊãâÂà∑Êñ∞
			async onRefresh() {
				this.isRefreshing = true
				await this.resetAndLoad()
				this.isRefreshing = false
			},

			// ‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö
			onLoadMore() {
				if (!this.loadingMore && this.hasMore && !this.loading) {
					this.loadActivities(true)
				}
			},

			// Êü•ÁúãÊ¥ªÂä®ËØ¶ÊÉÖ
			viewActivity(activity) {
				uni.navigateTo({
					url: `/pages/activity-detail/activity-detail?id=${activity.id}`
				})
			},

			// ÂàõÂª∫Ê¥ªÂä®
			createActivity() {
				uni.navigateTo({
					url: '/pages/activity-create/activity-create'
				})
			},

			// Ê†ºÂºèÂåñÊó•Êúü
			formatDate(date) {
				return formatDate(date, 'MMÊúàDDÊó• HH:mm')
			},

			// Ëé∑ÂèñÊ¥ªÂä®Áä∂ÊÄÅ‰ø°ÊÅØ
			getStatusInfo(activity) {
				const now = new Date()
				const startTime = new Date(activity.start_time)
				const endTime = new Date(activity.end_time)

				if (now < startTime) {
					return { text: 'Âç≥Â∞ÜÂºÄÂßã', color: '#007aff' }
				} else if (now >= startTime && now <= endTime) {
					return { text: 'ËøõË°å‰∏≠', color: '#ff9500' }
				} else {
					return { text: 'Â∑≤ÁªìÊùü', color: '#8e8e93' }
				}
			},

			// Ëé∑ÂèñÁ±ªÂûã‰ø°ÊÅØ
			getTypeInfo(type) {
				const typeMap = {
					meeting: { icon: 'üíº', name: '‰ºöËÆÆ' },
					event: { icon: 'üéâ', name: 'Ê¥ªÂä®' },
					training: { icon: 'üìö', name: 'ÂüπËÆ≠' },
					social: { icon: 'üçΩÔ∏è', name: 'Á§æ‰∫§' },
					sports: { icon: '‚öΩ', name: 'ËøêÂä®' },
					travel: { icon: 'üèñÔ∏è', name: 'ÊóÖË°å' },
					workshop: { icon: 'üîß', name: 'Â∑•‰ΩúÂùä' },
					conference: { icon: 'üé§', name: '‰ºöËÆÆ' },
					other: { icon: 'üìÖ', name: 'ÂÖ∂‰ªñ' }
				}
				return typeMap[type] || { icon: 'üìÖ', name: 'Êú™Áü•' }
			}
		}
	}
</script>

<style scoped>
	.activity-page {
		background-color: #f5f5f5;
		min-height: 100vh;
		padding-bottom: 120rpx;
		display: flex;
		flex-direction: column;
	}

	.page-header {
		background-color: white;
		padding: 40rpx 30rpx 30rpx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1rpx solid #f0f0f0;
		flex-shrink: 0;
	}

	.filter-tabs {
		background-color: white;
		padding: 20rpx 30rpx;
		border-bottom: 1rpx solid #f0f0f0;
		flex-shrink: 0;
	}

	.filter-scroll {
		white-space: nowrap;
	}

	.filter-item {
		display: inline-block;
		padding: 16rpx 32rpx;
		margin-right: 20rpx;
		background-color: #f8f9fa;
		border-radius: 30rpx;
		transition: all 0.3s;
	}

	.filter-item.active {
		background-color: #007aff;
	}

	.filter-text {
		font-size: 28rpx;
		color: #666;
	}

	.filter-item.active .filter-text {
		color: white;
	}

	.activity-list {
		flex: 1;
		overflow-y: auto;
		padding: 20rpx;
		height: calc(100vh - 240rpx);
	}

	.page-title {
		font-size: 36rpx;
		font-weight: bold;
		color: #333;
	}

	.header-actions {
		display: flex;
		align-items: center;
		gap: 20rpx;
	}

	.search-btn {
		width: 60rpx;
		height: 60rpx;
		border-radius: 30rpx;
		background-color: #f8f9fa;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all 0.3s;
	}

	.search-btn:active {
		background-color: #e9ecef;
	}

	.search-icon {
		font-size: 28rpx;
		color: #666;
	}

	.activity-list {
		padding: 20rpx;
	}

	.activity-card {
		background-color: white;
		border-radius: 16rpx;
		margin-bottom: 20rpx;
		overflow: hidden;
		box-shadow: 0 2rpx 8rpx rgba(0, 0, 0, 0.1);
	}

	.activity-header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 20rpx 30rpx;
		background-color: #f8f9fa;
	}

	.activity-sequence {
		font-size: 24rpx;
		font-weight: bold;
		color: #007aff;
		background-color: #e7f0ff;
		padding: 4rpx 12rpx;
		border-radius: 12rpx;
		margin-right: 12rpx;
	}

	.activity-type {
		font-size: 32rpx;
	}

	.activity-status {
		font-size: 26rpx;
		font-weight: bold;
	}

	.activity-content {
		padding: 30rpx;
	}

	.activity-title {
		font-size: 32rpx;
		font-weight: bold;
		color: #333;
		display: block;
		margin-bottom: 16rpx;
		line-height: 1.4;
		word-wrap: break-word;
		word-break: break-all;
		/* ÈôêÂà∂ÊúÄÂ§öÊòæÁ§∫2Ë°å */
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		overflow: hidden;
	}

	.activity-desc {
		font-size: 28rpx;
		color: #666;
		line-height: 1.5;
		display: block;
		margin-bottom: 20rpx;
		word-wrap: break-word;
		word-break: break-all;
		/* ÈôêÂà∂ÊúÄÂ§öÊòæÁ§∫3Ë°å */
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		overflow: hidden;
	}

	.activity-details {
		margin-bottom: 20rpx;
	}

	.detail-item {
		display: flex;
		align-items: center;
		margin-bottom: 12rpx;
	}

	.detail-icon {
		font-size: 28rpx;
		margin-right: 12rpx;
		width: 32rpx;
	}

	.detail-text {
		font-size: 26rpx;
		color: #666;
		flex: 1;
		word-wrap: break-word;
		word-break: break-all;
		line-height: 1.3;
	}

	.activity-footer {
		border-top: 1rpx solid #f0f0f0;
		padding-top: 20rpx;
	}

	.creator {
		font-size: 24rpx;
		color: #999;
	}

	.empty-state {
		text-align: center;
		padding: 100rpx 40rpx;
	}

	.empty-icon {
		font-size: 80rpx;
		display: block;
		margin-bottom: 20rpx;
	}

	.empty-title {
		font-size: 32rpx;
		color: #333;
		display: block;
		margin-bottom: 16rpx;
	}

	.empty-subtitle {
		font-size: 28rpx;
		color: #666;
		display: block;
	}

	.loading-state {
		text-align: center;
		padding: 60rpx;
		color: #666;
	}

	.load-more {
		text-align: center;
		padding: 40rpx;
		color: #999;
	}

	.no-more {
		text-align: center;
		padding: 40rpx;
		color: #999;
		font-size: 24rpx;
	}

	.create-btn {
		position: fixed;
		bottom: 40rpx;
		right: 40rpx;
		width: 120rpx;
		height: 120rpx;
		background-color: #007aff;
		border-radius: 60rpx;
		display: flex;
		align-items: center;
		justify-content: center;
		box-shadow: 0 4rpx 12rpx rgba(0, 122, 255, 0.3);
		z-index: 100;
	}

	.create-icon {
		font-size: 48rpx;
		color: white;
		font-weight: bold;
	}
</style>
